.list{
margin-top: 10px;
display: flex;
flex-direction: column;
justify-content: center;

}
.list  button {
   text-decoration: none;
  outline: none;
  display: inline-block;
  padding:  10px 20px;
  margin-left: 10px;
  position: relative;
  color: white;
  font-size:10px ;
  border: 1px solid rgba(255,255,255,.4);
  background-color: rgba(78, 39, 252, 0.4);
  border-radius: 40%;
  font-weight: 300;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;

}
/* Animation */
.enter {
  transform: translateX(-100%);
}
.enterActive {
  transform: translateX(0);
  transition: all 250ms linear;
}
.exit {
  transform: translateX(0);
}
.exitActive {
  transform: translateX(-100%);
  transition: all 250ms linear;
}
